.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: -10px;
}

.card-container::after {
  content: "";
  width: calc((100% - 10px)/3);
  display: block;
}

.card {
  display: flex;
  flex-direction: column;
  border-radius: 4px;
  width: calc((100% - 10px)/3);
  box-shadow: 0 4px 4px -4px rgba(26,60,77,.4);
  overflow: hidden;
  margin-top: 10px;
}

.card__imag img {
  width: 100%;
  height: 100%;
  -o-object-fit: contain;
  object-fit: contain;
}

.card__title {
  font-size: 20px;
}

.food__result {
  margin-top: 20px;
  padding: 1em;
  position: -webkit-sticky;
  position: sticky;
  bottom: 10px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 100%;
  background-color: #f0ed44;
  box-shadow: 0 4px 6px 0px rgba(37,37,37,.5);
  outline: 2px solid #000;
  outline-offset: -4px;
}

.result__total {
  font-size: 16px;
  width: 100%;
  text-align: left;
  display: inline-block;
  white-space: nowrap;
}

.result__sum {
  display: inline-block;
  padding: 0 .5em;
}

.result__select {
  margin-top: 1em;
  text-align: left;
  align-self: flex-start;
  font-size: 14px;
}

.result__list {
  display: block;
  min-height: 1em;
  line-height: 1;
}

@media (min-width: 960px) {
  .card-container {
    margin-top: -20px;
    width: 80%;
    margin-right: auto;
    margin-left: auto;
  }

  .card-container::after {
    width: calc((100% - 40px)/3);
  }

  .card {
    width: calc((100% - 40px)/3);
    margin-top: 20px;
  }

  .food__result {
    margin-top: 40px;
    padding: 1em;
    outline: 2px solid #000;
    outline-offset: -6px;
  }

  .result__total {
    font-size: 18px;
  }

  .result__select {
    font-size: 16px;
  }

  .result__list {
    line-height: 1.2;
    min-height: 1.5em;
  }
}